import { Button, Grid, TextField } from '@mui/material';
import { Link } from 'react-router-dom';
import LoginIcon from '@mui/icons-material/Login';
import LogoDC from "../../assets/log/doctors.svg";

const Support = () => {

    return (

        <Grid container justifyContent="center">
          <Grid item xs={5} sm={5} mb={5} marginTop={2}>
            <img src={LogoDC} alt="logoDC"></img>
          </Grid>


          <Grid item xs={ 12 } sx={{ mt: 0 }}>
            <TextField 
              label="Nombre completo" 
              type="text" 
              placeholder='Nombre completo' 
              fullWidth
              required
            />
          </Grid>

          <Grid item xs={ 12 } sx={{ mt: 2 }}>
            <TextField 
              label="Teléfono/Celular" 
              type="text" 
              placeholder='Teléfono/Celular' 
              fullWidth
              required
            />
          </Grid>

          <Grid item xs={ 12 } sx={{ mt: 2 }}>
            <TextField 
              label="Correo" 
              type="email" 
              placeholder='correo@google.com' 
              fullWidth
              required
            />
            </Grid>

          <Grid item xs={ 12 } sx={{ mt: 2 }}>
            <TextField
              id="outlined-multiline-static"
              label="Comentario"
              multiline
              rows={4}
              defaultValue=""
              fullWidth
              required
            />
          </Grid>

          <Grid
              container
              justifyContent="center"
              spacing={2}
              sx={{ mb: 2, mt: 1 }}
            >
              <Grid item xs={12} sm={6}>
                <Button variant="contained" fullWidth sx={{ mb: 2, mt: 0, borderRadius: 2, }} size="large">
                  Enviar
                </Button>
              </Grid>
            </Grid>


            <Grid container direction='row' justifyContent='left' >
                <Link to="/login"  style={{color: '#9C239C'}} activeStyle={{color: '#E3F2FD'}}>
              <Button startIcon={<LoginIcon titleAccess='inicioSesión'></LoginIcon>}>
                <Link to="/login"  style={{color: '#9C239C'}} activeStyle={{color: '#E3F2FD'}}>
                  Iniciar sesión
                </Link>
              </Button>
                </Link>
            </Grid>

        </Grid>

      );
}

export default Support